import React, { useEffect } from 'react'
import './HomeBanner.scss';

export default function HomeBanner() {
    useEffect(() => {
        function parallax(e: MouseEvent) {
            const elem: HTMLElement = document.querySelector("#HomeBanner") as HTMLElement;
            let width = window.innerWidth / 2;
            let height = window.innerHeight / 2;
            let mouseMoveX = e.clientX;
            let mouseMoveY = e.clientY;
            let _depth1 = `${50 - (mouseMoveX - width) * 0.01}% ${50 - (mouseMoveY - height) * 0.01}%`;
            let _depth2 = `${50 - (mouseMoveX - width) * 0.02}% ${50 - (mouseMoveY - height) * 0.02}%`;
            let _depth3 = `${50 - (mouseMoveX - width) * 0.06}% ${50 - (mouseMoveY - height) * 0.06}%`;
            let x = `${_depth3}, ${_depth2}, ${_depth1}`;
            if (elem) {
                elem.style.backgroundPosition = x;
            }
        }
        function TextAnimation() {
            var el: HTMLElement = document.getElementsByClassName('animated-text')[0] as HTMLElement;
            if (el) {
                var text = el.innerHTML;
                el.innerHTML = "";

                for (var i = 0; i < text.length; i++) {
                    var childEl = document.createElement('span');
                    childEl.className = el.className + '-character';
                    childEl.style.animationDelay = (i + 1) * .075 + 's';
                    childEl.innerHTML = text[i];
                    el.appendChild(childEl);
                }
            }
        }
        document.addEventListener("mousemove", parallax);
        // document.addEventListener("load", TextAnimation);
        TextAnimation();
    });
    return (
        <div id="HomeBanner" className='main'>
            <h1 className="text h-100 d-flex align-items-center justify-content-center">Techiera</h1>
        </div>
    )
}
