import { getActiveElement } from '@testing-library/user-event/dist/utils';
import React, { MouseEventHandler, useEffect } from 'react'
import { data } from './data';
import './Portfolio.scss';

export default function PortfolioAcc() {
    const ActiveElement: MouseEventHandler<HTMLDivElement> = (e) => {
        let removeActive = Array.from(document.getElementsByClassName("gallery-item active"));
        for (let elRemoveCl of removeActive) {
            elRemoveCl.classList.remove("active");
        }
        if (e && e.target) {
            const el = e.target as HTMLDivElement;
            if (el && el.parentElement)
                el.parentElement.classList.add("active");
        }
    }
    return (
        <div className='portfolio main' id='portfolio'>
            <div className="gallery-container">
                {data.map((d, i) => {

                    return (
                        <div className={i === 0 ? "gallery-item active" : "gallery-item"} key={d.id} onClick={e => ActiveElement(e)}>
                            <h1 className="gallery-item__text">{d.text}</h1>
                            <img className="gallery-item__img" src={d.img} />
                        </div>
                    );
                })}
            </div>
        </div>
    )
}
